<template>
  <v-sheet class="ma-2 elevation-7 pa-4 pb-4">
    <form @submit.prevent="submit">
      <v-text-field
        v-model="enquiry.name"
        :counter="10"
        :error-messages="name.errorMessage.value"
        label="Name"
        variant="outlined"
      ></v-text-field>

      <v-text-field
        v-model="enquiry.phoneNumber"
        :counter="7"
        :error-messages="phone.errorMessage.value"
        label="Phone Number"
        variant="outlined"
      ></v-text-field>
  

      <v-text-field
        v-model="enquiry.email"
        :error-messages="email.errorMessage.value"
        label="E-Mail"
        variant="outlined"
      ></v-text-field>

      <v-textarea
        v-model="enquiry.enquiryMessage"
        label="Enquiry Message"
        variant="outlined"
      ></v-textarea>

      <v-container fluid>
        <v-row no-gutters class="d-flex justify-center">
          <v-col cols="12" class="d-flex justify-center mb-4">
            <v-btn
              type="submit"
              variant="flat"
              color="pink-accent-3"
              width="300px"
              @click.prevent="showDetails()"
            >
            
              submit
            </v-btn>
          </v-col>

          <v-col cols="12" class="d-flex justify-center">
            <v-btn
              @click="handleReset"
              variant="flat"
              color="pink-accent-3"
              width="300px"
            >
              clear
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </form>
  </v-sheet>
</template>

<script setup>
import { useField, useForm } from "vee-validate";
import { reactive } from "vue";

const { handleReset } = useForm({
  validationSchema: {},
});

const name = useField("name");
const phone = useField("phone");
const email = useField("email");

const enquiry = reactive([
  {
    name: "",
    phoneNumber: "",
    email: "",
    enquiryMessage: "",
  },
]);



function showDetails() {
  const whatsApp = `https://wa.me/9559812361?text= Name of the User: ${enquiry.name} \n Contact: ${enquiry.phoneNumber} \n email: ${enquiry.email} \n enquiryMessage: ${enquiry.enquiryMessage}`
  window.open(whatsApp, '_blank').focus();
}
</script>